* {
  padding: 0;
  margin: 0;
  border: 0;
}

*, *:before, *:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus, :active {
  outline: none;
}

a:focus, a:active {
  outline: none;
}

nav, footer, header, aside {
  display: block;
}

html, body {
  height: 100%;
  width: 100%;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input, button, textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a, a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style-type: none;
}

img {
  vertical-align: top;
}

@font-face {
  font-family: "OpenSans";
  src: url("../fonts/OpenSans-Light.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
body, h1, h2, h3, h4, h5, h6, p, div {
  margin: 0;
  padding: 0;
  font-family: "OpenSans", sans-serif;
  font-size: 16px;
  line-height: 22px;
  font-style: inherit;
}

.header {
  background-color: #fff;
  position: fixed;
  width: 100%;
  height: 100px;
  box-shadow: 0 5px 5px rgba(0, 0, 0, 0.1);
  z-index: 2000;
}
.header__container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  max-width: 1250px;
  height: 100%;
  margin: 0 auto;
  padding: 0 15px;
}
.header__container-right {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__logo {
  margin-top: 10px;
  width: 80px;
  height: 80px;
}

@media (max-width: 425px) {
  .header {
    height: 70px;
  }
  .header__logo {
    width: 60px;
    height: 60px;
    margin-top: 5px;
  }
}
.menu {
  background-color: #ffffff;
}
.menu ul {
  display: flex;
  flex-direction: row;
}
.menu ul li {
  display: flex;
  align-items: center;
  padding: 0 14px;
  margin-left: 8px;
  justify-content: center;
}
.menu ul li:first-child {
  margin-left: 0;
}
.menu ul li:last-child {
  width: 140px;
  height: 40px;
  background-color: #337ab7;
  border-radius: 30px;
}
.menu ul li:last-child a {
  color: #ffffff;
}
.menu ul li:last-child a:hover {
  color: #ffffff;
}
.menu ul li:last-child:hover {
  background-color: #285f8f;
}
.menu ul li a {
  font-size: 15px;
  color: #666666;
}
.menu ul li a:hover {
  color: #337ab7;
}
.menu ul li:hover {
  background-color: #f4f4f4;
}

@media (max-width: 768px) {
  .menu {
    display: none;
  }
  .menu.active {
    width: 100%;
    height: 260px;
    display: flex;
    position: absolute;
    justify-content: center;
    left: 0;
    top: 100px;
  }
  .menu.active ul {
    flex-direction: column;
  }
  .menu.active ul li {
    width: 140px;
    margin: 20px 0 0 0;
    padding: 0;
  }
  .menu.active ul li:first-child {
    margin-top: 0;
  }
  .menu.active ul li:last-child {
    margin-top: 10px;
  }
}
@media (max-width: 425px) {
  .menu.active {
    height: 260px;
    top: 70px;
  }
  .menu.active ul li {
    font-size: 14px;
  }
  .menu.active ul li:first-child {
    margin-top: 3px;
  }
  .menu.active ul li:last-child {
    margin-top: 10px;
  }
}
.burger {
  display: none;
  background-color: #337ab7;
  width: 42px;
  height: 32px;
  border-radius: 5px;
  cursor: pointer;
}
.burger .burger__line {
  width: 22px;
  height: 2px;
  background-color: #fff;
  margin: 4px 10px 0 10px;
}
.burger .burger__line:first-child {
  margin-top: 9px;
}

@media (max-width: 768px) {
  .burger {
    display: block;
  }
}
.content {
  padding-top: 100px;
  height: 765px;
  width: 100%;
  background-color: #f7f7f7;
}
.content__container {
  display: flex;
  flex-direction: row;
  height: 100%;
  max-width: 1250px;
  margin: auto;
  padding: 0 15px;
}
.content__container-left {
  width: 50%;
  margin: 215px 0 0 37px;
  padding-right: 0;
}
.content__container-left h1 {
  width: 100%;
  font-size: 35px;
  letter-spacing: 1px;
  line-height: 35px;
}
.content__container-left p {
  max-width: 500px;
  font-size: 18px;
  letter-spacing: 0;
  line-height: 32px;
  font-weight: 100;
  text-align: center;
  margin: 22px 0 0 18px;
}
.content__container-right {
  display: flex;
  flex-direction: column;
  width: 50%;
  margin: 55px 0 0 0;
}
.content__container-right .content__title {
  width: 100%;
  letter-spacing: 2px;
  font-size: 20px;
}
.content__container-right .form {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%;
}
.content__container-right .form__submit-btn {
  height: 50px;
  width: 370px;
  border-radius: 25px;
  color: #ffffff;
  background-color: #337ab7;
  font-size: 13px;
  letter-spacing: 1px;
  margin: 30px 15px 0 15px;
  font-weight: 1000;
  text-transform: uppercase;
}
.content__container-right .form__field {
  width: 100%;
  height: max-content;
  margin-top: 7px;
}
.content__container-right .form__field:nth-child(6) {
  margin-top: 15px;
  height: max-content;
}
.content__container-right .form__field:nth-child(6) img {
  margin-top: 8px;
}
.content__container-right .form__field img {
  width: 7px;
  height: 7px;
  margin-left: 3px;
}
.content__container-right .form__label {
  display: flex;
  align-items: center;
  margin: 1px 0 5px 0;
  height: max-content;
  width: 100%;
  font-size: 15px;
  letter-spacing: 0;
}
.content__container-right .form__label.first {
  margin-top: 10px;
}
.content__container-right .form__input {
  width: 100%;
  height: 50px;
  border: #bababa 1px solid;
  border-radius: 5px;
  padding: 10px;
}
.content__container-right .form__input:focus {
  border: 1px solid #66afe9;
  box-shadow: 0 0 5px 0 #66afe9;
}
.content__container-right .form__input:focus::placeholder {
  color: #ffffff;
}
.content__container-right .form__checkbox {
  display: flex;
  margin-top: 9px;
}
.content__container-right .form__checkbox-text {
  width: 100%;
  align-items: center;
  font-size: 14px;
  margin-left: 7px;
}
.content__container-right .form__checkbox-text a {
  padding: 0 2px;
}

@media (max-width: 1440px) {
  .content__container h1 {
    font-size: 30px;
  }
  .content__container p {
    font-size: 15px;
  }
}
@media (max-width: 1024px) {
  .content__container-left {
    margin: 100px 0 0 0;
    display: flex;
    align-items: center;
    flex-direction: column;
  }
}
@media (max-width: 768px) {
  .content {
    height: 1130px;
  }
  .content__title {
    text-align: center;
  }
  .content__container {
    flex-direction: column;
  }
  .content__container-left {
    width: 100%;
    margin: 40px 0 50px 0;
  }
  .content__container h1 {
    text-align: center;
  }
  .content__container-right {
    width: 100%;
    margin: 0;
  }
  .content__container .form {
    width: 100%;
  }
  .content__container .form__field {
    width: 100%;
  }
  .content__container .form__input {
    width: 100%;
  }
}
@media (max-width: 500px) {
  .content {
    height: 1230px;
  }
}
@media (max-width: 425px) {
  .content__container-left h1 {
    line-height: 35px;
  }
  .content__container-right .form__submit-btn {
    width: 275px;
  }
}
@media (max-width: 375px) {
  .content {
    height: 1280px;
  }
  .content__container-left p {
    font-size: 13px;
  }
  .content__container-left h1 {
    font-size: 23px;
  }
}
@media (max-width: 320px) {
  .content {
    height: 1290px;
  }
}
.course {
  width: 100%;
  height: 560px;
  background-color: #ffffff;
}
.course__content {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
}
.course__container {
  height: 100%;
  max-width: 1250px;
  margin: auto;
  padding: 0 15px;
}
.course__container .slick-dots {
  display: flex;
  justify-content: center;
  align-items: center;
}
.course__container .slick-initialized .slick-slide {
  display: flex;
}
.course__container li {
  background-color: #b6b6b6;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  margin: 40px 0 0 14px;
}
.course__container li:nth-child(1) {
  margin-left: 0;
}
.course__container li.slick-active {
  background-color: #666666;
  width: 12px;
  height: 12px;
  border-radius: 50%;
}
.course__container button {
  font-size: 0;
}
.course__title {
  width: 100%;
  text-align: center;
  padding: 80px 0 40px 0;
}
.course__title h1 {
  font-size: 33px;
  letter-spacing: 1.5px;
}
.course__item {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 375px;
  margin: 0 15px 0 15px;
}
.course__subtitle {
  text-align: center;
  color: #666666;
  padding: 25px 0 21px 0;
  font-size: 25px;
  border-bottom: #3498db 1px solid;
}
.course__description {
  color: #a3a3a3;
  text-align: center;
  width: 345px;
  padding-top: 27px;
  line-height: 25px;
}

@media (max-width: 1440px) {
  .course__subtitle {
    font-size: 20px;
  }
  .course__description {
    width: 300px;
  }
}
@media (max-width: 850px) {
  .course {
    height: 590px;
  }
}
@media (max-width: 425px) {
  .course__title h1 {
    font-size: 23px;
  }
}
@media (max-width: 375px) {
  .course__subtitle {
    padding-bottom: 5px;
  }
  .course__description {
    font-size: 14px;
    width: 260px;
    padding-top: 15px;
  }
}
.statistic {
  height: 400px;
  width: 100%;
  background-color: #337ab7;
}